@import url("https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap");

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  background: #181d1f;
  color: #ccc;
  padding: 5px 20px;
  margin: 0;
  font-weight: normal;
  box-shadow: 0 0 4px #130b00;
  z-index: 20;
  line-height: 100%;

  > .logo {
    display: flex;
    align-items: center;
    background-color: #252b2d;
    border-radius: 5px;
    padding: 3px 5px 3px 3px;
    margin: 3px 0;
    border: 0;

    > h1 {
      padding: 0 3px;
      font-size: 14pt;
      margin: 0;
      font-weight: normal;
      color: #fff;
      font-family: "Bebas Neue", monospace;
    }
  }
}

footer {
  color: #888;
  margin-top: 30px;
  padding: 0 20px 20px 20px;
  font-size: 10pt;
  font-weight: normal;
  text-align: right;
}

article {
  max-width: 900px;
  margin: 20px auto;
  font-size: 120%;

  h1::before,
  h2::before {
    content: "> [";
    color: #00fefe;
  }

  h1::after,
  h2::after {
    content: "]";
    color: #00fefe;
  }

  h3::before {
    content: "> ";
    color: #00fefe;
  }
}

section {
  h2 {
    font-family: "Major Mono Display", monospace;
  }
}

.page-header {
  background: #181d1f;
  display: flex;
  justify-content: center;

  > div {
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
    max-width: 100%;
    padding: 40px 0;

    > img {
      width: 25%;
    }

    > div {
      width: 60%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;

      > h1 {
        font-size: min(7.5vw, 75px);
        line-height: 1em;
        margin: 0;
        margin-bottom: 15px;
        font-family: "Major Mono Display", monospace;
      }

      > div {
        font-size: min(2vw, 20px);
        line-height: 1.1;
      }
    }
  }
}

.page {
  .link {
    height: 75px;

    .link-description {
      width: 280px;
      font-weight: normal;
    }
  }

  section {
    background-color: #252b2d;
    border-radius: 0px;
    margin: 25px 0;
    padding: 0.1px 15px;
  }
}

.link {
  background-color: #252b2d;
  border-radius: 0px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;

  height: 95px;
  margin: 10px 0;
  padding: 0 15px;

  &:link,
  &:visited {
    border-bottom: none;
    color: #ccc;
  }

  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
  }

  .link-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 15px;

    font-size: 150%;
    font-family: "Major Mono Display", monospace;

    svg {
      color: #00fefe;
      font-size: 40px;
    }
  }

  .nobefore::before {
    content: "";
  }

  .link-description {
    color: #7e7e7e;
  }
}

.down-enter {
  opacity: 0;
  transform: scale(0.9);
}
.down-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.down-exit {
  opacity: 1;
}
.down-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
